.App{
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.wrapper {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  line-height: 1.6;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 120px);
}

.chat button{
  display: flex;
    justify-content: center;
    align-items: center;
    border-radius: var(50%);
    background-color: var(#125317);
    transition: background-color 150ms, box-shadow 150ms;
    height: var(60px);
    width: var(60px);
    border: 1px solid var(rgba(0, 0, 0, 0.04));
    box-shadow: 0 1px 6px var(rgba(0, 0, 0, 0.06)), 0 2px 24px var(rgba(0, 0, 0, 0.08));
}
